import React from "react"
import Typist from "react-typist"

import Layout from "../components/Layout"
import { about, experiences, social_links } from "../constants"

export default function About() {
  return (
    <Layout>
      <div className="about-container">
        <div className="about">
          <Typist
            cursor={{
              show: false,
            }}
          >
            <Typist.Delay ms={100} />
            <h2>{about.heading}</h2>
          </Typist>
          <p className="fade-in">{about.description}</p>
          <ul className="fade-in">
            {social_links.map(social_link => (
              <li>
                <a rel="noreferrer" href={social_link.link} target="_blank">
                  <i className={`fa ${social_link.icon}`} aria-hidden="true" />
                </a>
              </li>
            ))}
          </ul>
        </div>
        <div className="experience">
          <Typist
            cursor={{
              show: false,
            }}
          >
            <h2>Experience</h2>
          </Typist>
          <ul className="fade-in">
            {experiences.map((experience, index) => (
              <li key={index}>
                {experience.title} <br />
                <small>-- {experience.industry}</small>
              </li>
            ))}
          </ul>
        </div>
      </div>
    </Layout>
  )
}
